body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

fieldset, img, abbr {
  border: 0; }

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal; }

ul li {
  list-style: none; }

caption, th {
  text-align: left; }

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal; }

sup {
  vertical-align: text-top; }

sub {
  vertical-align: text-bottom; }

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit; }

legend {
  color: #000; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block; }

img {
  max-width: 100%;
  height: auto; }

body, html {
  padding: 0;
  margin: 0; }

img {
  max-width: 100%; }

.proportion {
  position: relative; }

.ratio {
  padding-top: 61%;
  height: 0; }

.container {
  max-width: 955px;
  margin: 0 auto;
  padding: 15px 0; }

.flex-wrap {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  align-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

.flex1, .flex3 {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 0 1 30%;
  margin: 0 1%; }

.flex2 {
  display: flex;
  flex: 0 1 35%; }

@media only screen and (max-width: 690px) {
  .flex-wrap {
    flex-direction: column; }
  .ratio {
    padding-top: 118.7%; }
  .flex1 {
    margin: 0 0.6%;
    width: 29%;
    flex: 0 1 50%;
    align-items: flex-start; }
  .flex2 {
    width: 66%;
    order: -1;
    flex: 0 1 100%;
    margin: 0 0.72%;
    align-items: flex-end; }
  .flex3 {
    width: 29%;
    flex: 0 1 50%;
    margin: 0 .6%;
    align-items: flex-start; } }

@media only screen and (max-width: 480px) {
  .flex-wrap {
    flex-direction: row;
    align-content: center;
    width: auto;
    height: auto; }
  .flex2 {
    width: 100%; }
  .proportione {
    position: inherit; }
  .flex1, .flex3 {
    flex: 1 1 200px; } }
